.circularText {
  margin: 0 auto;
  border-radius: 50%;
  width: 160px;
  position: relative;
  height: 160px;
  font-weight: 900;
  color: #fff;
  text-align: center;
  cursor: pointer;
  transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
}

.letter {
  position: absolute;
  display: inline-block;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  font-size: 20px;
  transition: all 0.5s cubic-bezier(0, 0, 0, 1);
}
